<template>
  <!-- modal login-->
  <b-modal
    id="modal-habitacion-residente-form"
    ref="refModalHabitacionResidenteForm"
    v-model="dialog"
    :title="formTitle"
    :header-class="'d-flex justify-content-center text-primary'"
    size="xl"
    no-close-on-backdrop
    :hide-footer="true"
    no-close-on-esc
    hide-header-close
  >
    <b-form @submit.prevent="onSubmit">
      <!-- <b-row class="bg-light-primary pa-0 ma-0">
        <b-col>
          <h4 class="text-center">
            CASA 01 - N°. CA01 - 101
          </h4>
        </b-col>
      </b-row> -->
      <b-row>
        <b-col md="6">
          <b-form-group
            label="modalidad"
            label-class="text-uppercase"
            label-size="sm"
          >
            <v-select
              v-model="residenteHabitacionData.tipo_id"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="modalidades"
              label="nombre"
              :reduce="val => val.id"
              placeholder="Seleccionar"
              @input="setTipoHabitacionSelect"
            />
          </b-form-group>
        </b-col>
        <b-col md="6">
          <b-form-group
            label="convocatoria"
            label-class="text-uppercase"
            label-size="sm"
          >
            <v-select
              v-model="residenteHabitacionData.tipo_id"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="modalidades"
              label="nombre"
              :reduce="val => val.id"
              placeholder="Seleccionar"
              @input="setTipoHabitacionSelect"
            />
          </b-form-group>
        </b-col>
      </b-row>

      <!-- Residentes -->
      <b-row>
        <b-col
          sm="12"
          md="12"
          lg="12"
        >
          <b-row no-gutters>
            <b-col
              lg="10"
              sm="10"
            >
              <label class="text-uppercase col-form-label-sm">Persona</label>
              <vue-autosuggest
                ref="inputResidencia"
                v-model="query"
                :suggestions="suggestions"
                :render-suggestion="renderSuggestion"
                :get-suggestion-value="getSuggestionValue"
                :input-props="{id:'autosuggest__input', placeholder:'', class: 'form-control'}"
                @selected="setResidenciaSelect"
                @input="fetchResults"
                @focus="fetchResults"
              />

            </b-col>

            <b-col
              lg="2"
              sm="2"
              class="mt-2"
            >
              <b-button
                class="mb-0"
                variant="success"
                block
                @click="crearResidencia"
              >
                <feather-icon
                  icon="SaveIcon"
                  size="16"
                />
              </b-button>
            </b-col>
          </b-row>
        </b-col>

      </b-row>

      <!-- Fechas -->
      <b-row class="pt-1">
        <!-- Fecha ingreso -->
        <b-col cols="4">
          <b-form-group
            label="Fecha ingreso"
            label-class="text-uppercase"
            label-size="sm"
          >
            <b-form-input
              v-model="residenteHabitacionData.codigo"
              placeholder="Nombre / Código"
            />
          </b-form-group>
        </b-col>
        <!-- Fecha salida -->
        <b-col cols="4">
          <b-form-group
            label="Fecha salida"
            label-class="text-uppercase"
            label-size="sm"
          >
            <b-form-input
              v-model="residenteHabitacionData.codigo"
              placeholder="Nombre / Código"
            />
          </b-form-group>
        </b-col>
        <!-- Fecha ingreso -->
        <b-col cols="4">
          <b-form-group
            label="Fecha ingreso"
            label-class="text-uppercase"
            label-size="sm"
          >
            <b-form-input
              v-model="residenteHabitacionData.codigo"
              placeholder="Nombre / Código"
            />
          </b-form-group>
        </b-col>

        <!-- submit and reset -->
        <b-col cols="12">
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            type="submit"
            variant="primary"
            class="mr-1"
          >
            {{ titleButton }}
          </b-button>
          <b-button
            v-ripple.400="'rgba(186, 191, 199, 0.15)'"
            type="reset"
            variant="outline-secondary"
            @click="close"
          >
            Cerrar
          </b-button>
        </b-col>
      </b-row>
    </b-form>
    <b-table
      class="mt-1"
      striped
      hover
      :items="items"
      :fields="fields"
    >
      <template #cell(actions)="row">
        <b-button
          size="sm"
          class="mr-1"
          @click="info(row.item, row.index, $event.target)"
        >
          Retirar
        </b-button>
        <b-button
          size="sm"
          @click="row.toggleDetails"
        >
          {{ row.detailsShowing ? 'Ocultar' : 'Mostrar' }} Bienes
        </b-button>
      </template>
      <template #row-details="row">
        <b-card>
          <ul>
            <li
              v-for="(value, key) in row.item"
              :key="key"
            >
              {{ key }}: {{ value }}
            </li>
          </ul>
        </b-card>
      </template>
    </b-table>
  </b-modal>

</template>

<script>
import {
  BRow, BCol, BForm, BModal, BFormGroup, BButton, BFormInput, BTable,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import {
  ref, watch, toRefs, nextTick, computed,
} from '@vue/composition-api'
import store from '@/store'
// Notification
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import { VueAutosuggest } from 'vue-autosuggest'
import vSelect from 'vue-select'

export default {
  components: {
    VueAutosuggest,
    vSelect,
    BRow,
    BCol,
    BModal,
    BForm,
    BFormGroup,
    BTable,
    // BFormTextarea,
    BButton,
    BFormInput,
    // BInputGroup,
    // BInputGroupPrepend,
    // BInputGroupAppend,
  },
  directives: {
    Ripple,
  },
  props: {
    persona: {
      type: Object,
      default() {
        return { }
      },
    },
    editedItem: {
      type: Object,
      default() {
        return { }
      },
    },
  },
  data() {
    return {
      tipos: [],
      query: '',
      selected: null,
      timeout: null,
      debounceMilliseconds: 500,
      suggestions: [],
      modalidades: [],
      items: [
        { age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
        { age: 21, first_name: 'Larsen', last_name: 'Shaw' },
        { age: 89, first_name: 'Geneva', last_name: 'Wilson' },
        { age: 38, first_name: 'Jami', last_name: 'Carney' },
      ],
      fields: [
        { key: 'first_name', label: 'residente' },
        { key: 'modalidad', label: 'modalidad' },
        { key: 'fecha_ingreso', label: 'F. Ingreso' },
        { key: 'fecha_salida', label: 'F. Salida' },
        { key: 'actions', label: 'Acciones' },
      ],
      // form: {
      //   codigo: null,
      //   residencia_id: null,
      //   tipo_id: 2,
      //   nombre: null,
      //   estado: null,
      // },
    }
  },
  created() {
    this.$http.get('becas/modalidades').then(response => { this.modalidades = response.data.data })
    this.$http.get('habitaciones/tipos').then(response => { this.tipos = response.data.data })
  },
  methods: {
    crearResidencia() {
      const form = { nombre: this.$refs.inputResidencia.value }
      this.$http.post('residencias', form).then(response => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: `Registro exitoso! ${response.data.nombre}`,
            icon: 'EditIcon',
            variant: 'success',
          },
        })
      }).catch(error => {
        if (error.response.data.code === 422) {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: `${error.response.data.error.nombre}!!`,
              icon: 'EditIcon',
              variant: 'danger',
            },
          })
        }
        if (error.response.data.code === 409) {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: `${error.response.data.error}!!`,
              icon: 'EditIcon',
              variant: 'danger',
            },
          })
        }
      })
    },
    setResidenciaSelect(obj) {
      // console.log('setResidenciaSelect', obj.item, index)
      this.residenteHabitacionData.residencia_id = obj.item.id
    },
    setTipoHabitacionSelect(id) {
      // console.log('setTipoHabitacionSelect', id)
      this.residenteHabitacionData.tipo_id = id
    },
    renderSuggestion(suggestion) {
      // console.log('renderSuggestion', suggestion)
      // return suggestion.item.nombre
      // <div class="d-flex">
      //       <div>
      //         <p class="text-uppercase">{suggestion.item.nombre}</p>
      //       </div>
      //     </div>
      return (
        <div class="text-uppercase font-weight-bold">{suggestion.item.nombre}</div>
      )
    },
    getSuggestionValue(suggestion) {
      // console.log('getSuggestionValue', suggestion)
      return suggestion.item.nombre.toUpperCase()
    },
    fetchResults() {
      const { query } = this
      console.log(query)
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        const promise = this.$http.get(`habitaciones/?filter=${query}`)
        // Must return a promise that resolves to an array of items
        return promise.then(response => {
          console.log(response.data.data)
          this.suggestions = []
          this.selected = null
          // Pluck the array of items off our axios response
          // const { items } = response.data
          // this.suggestions = response.data.data
          this.suggestions = [{ data: response.data.data }]
          // Must return an array of items or an empty array if an error occurred
          return this.suggestions || []
        })
      }, this.debounceMilliseconds)
    },
  },
  setup(props, { emit }) {
    const { editedItem } = toRefs(props)
    const editedIndex = ref(-1)
    const titleButton = ref('Guardar')
    const dialog = ref(false)
    const toast = useToast()
    const query = ref('')

    // const blankresidenteHabitacionData = {
    //   id: 1,
    //   nombre: '',
    //   codigo: '',
    //   aforo: null,
    //   descripcion: null,
    //   estado: 'activo',
    // }

    const form = {
      codigo: null,
      residencia_id: null,
      tipo_id: null,
      aforo_total: null,
      // nombre: null,
      // estado: null,
    }

    const residenteHabitacionData = ref(JSON.parse(JSON.stringify(form)))

    const resetuserData = () => {
      residenteHabitacionData.value = JSON.parse(JSON.stringify(form))
    }

    const close = () => {
      dialog.value = false
      nextTick(() => {
        resetuserData()
        editedIndex.value = -1
      })
    }

    const formTitle = computed(() => (editedIndex.value === -1 ? 'CASA 01 - N°. CA01 - 101' : `Editar Asignacion de residente N° ${editedIndex.value}`))

    watch(dialog, () => {
      // eslint-disable-next-line no-unused-expressions
      dialog.value || close()
    })

    // watch(persona, () => {
    //   resetuserData()
    //   residenteHabitacionData.value.usuario_id = persona.value.usuario_id
    //   residenteHabitacionData.value.persona = persona.value
    //   editedIndex.value = -1
    // })

    watch(editedItem, () => {
      console.log('vemos para editar::::::::::', editedItem)
      editedIndex.value = editedItem.value.id
      // Datos de triaje
      residenteHabitacionData.value = editedItem.value
      query.value = editedItem.value.residencia.nombre.toUpperCase()
      console.log(editedItem.value.residencia.nombre)
      // residenteHabitacionData.value.persona = editedItem.value.persona_usuario
      // residenteHabitacionData.value.servidor_publico_id = editedItem.value.servidor_publico_id
      // residenteHabitacionData.value.usuario_id = editedItem.value.usuario_id
    })

    const onSubmit = () => {
      if (editedIndex.value > -1) {
      // vamos a editar
        console.log('Vamos a editar')
        store.dispatch('habitaciones/updatedHabitacion', residenteHabitacionData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: '¡Actualización exitosa',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            dialog.value = false
            emit('refresh-data-habitaciones-table')
          })
          .catch(error => {
            console.log(error)
          })
      } else {
        console.log('Vamos a un nuevo registro', residenteHabitacionData.value)
        store.dispatch('habitaciones/addHabitacion', residenteHabitacionData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: '¡Registro exitoso!',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            dialog.value = false
            emit('refresh-data-habitaciones-table')
          })
      }
    }

    return {
      residenteHabitacionData,
      resetuserData,
      onSubmit,
      close,
      dialog,
      formTitle,
      titleButton,
      query,
    }
  },

}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-autosuggest.scss';
@import '@core/scss/vue/libs/vue-select.scss';
  /* Number Type Input Box Scss for - Remove arrow on hover */
  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: auto !important;
  }
</style>
